body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Brandon", "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* background-image:url("./images/bg_color.svg"); */
  /* background-repeat: no-repeat; */
  /* background-size: cover; */
  /* background-position: center; */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
  font-family: 'Brandon';
  font-style: normal;
  font-weight: normal;
  src:
    local('Brandon reg'),
    local('Brandon-reg'),
    url(/static/media/Brandon_reg.1f55925e.otf) format('opentype');
}

@font-face {
  font-family: 'Brandon Bold';
  font-style: bold;
  font-weight: bold;
  src:
    local('Brandon blk'),
    local('Brandon-blk'),
    url(/static/media/Brandon_blk.c10f15f1.otf) format('opentype');
}

h1 {
  font-family: 'Brandon Bold';
}

h2, h3, h4, h5, p {
  font-family: 'Brandon';
}

.d-flex {
  display: flex;
  width: 100%;
}

.fw{
  flex-wrap: wrap;
}

.jcc {
  justify-content: center;
}

.aic {
  align-items: center;
}
/* Landing */

.home-landing {
  width: 100%;
  height: calc(100vh - 100px);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  align-items: center;
}

.home-landing img {
  width: 400px;
  height: auto;
}

.home-landing h3, h5 {
  color: #989899;
}

#logovideo {
  width: auto;
  height: 450px;
  left: 50%;
  position: absolute;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.footer {
  width: 100%;
  height: 100px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.social {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding-right: 100px;
}

.social img {
  margin: 20px 5px 0 5px;
  max-height: 30px;
  width: auto;
  height: 100%;
}

.home-carousel {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-content: center;
  align-items: center;
}

/* Objetivos */

.objetivos {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  align-items: center;
}

.obj-header {
  max-width: 800px;
  text-align: center;
  margin: 0 auto;
}

.obj-container {
  max-width: 1100px;
  display: flex;
  flex-wrap: wrap;
  flex-grow: 6;
  justify-content: center;
  align-content: center;
}

.obj-container img {
  max-width: 170px;
  height: auto;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

/* Video */
#expvideo {
  width: auto;
  height: 450px;
  left: 50%;
  position: absolute;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.video-container {
  width: 100%;
  height: 100%;
}

/* Newsletter */
.newsletter {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  /* align-content: center; */
  align-items: center;
}

.newsletter h1 {
  width: 100%;
  text-align: center;
  margin-top: 200px;
}

.news-container {
  display: flex;
  margin: 50px 120px;
}

.newscard {
  max-width: 300px;
  height: 500px;
  margin: 0 50px;
}

.newscard h3 {
  max-width: 280px;
  text-align: center;
  margin: 10px;
}

.newscard img {
  width: 300px;
  height: auto;
  max-height: 170px;
}

.newscard p {
  width: 300px;
  height: 100px;
  padding: 10px;
  overflow: hidden;
  text-align: justify;
}

.newscard button {
  width: 100px;
  height: auto;
}
.form {
  margin: 0 130px;
}

.ant-form-item {
  padding-bottom: 3px;
  margin-bottom: 3px;
}

.auth {
