/* Objetivo detail */

.objetivo-detail {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  align-items: center;
}

.objetivo-detail img {
  max-width: 172px;
  max-height: 117px;
  height: auto;
}
.nav {
  width: 100%;
  height: 70px;
  color: black;
  background-color: rgba(255, 255, 255, 0.95);
  display: flex;
  align-items: center;
  align-content: center;
  justify-content: center;
  position: fixed;
  top: 0;
  z-index: 100;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.nav div {
  display: flex;
  align-items: center;
  align-content: center;
  justify-content: center;
}

.nav img {
  height: 40px;
  margin-left: 20px;
}

.nav p {
  margin: 0 20px;
  cursor: pointer;
  font-size: 1rem;
  transition: all .5s ease;
}

.nav p:hover {
  font-weight: bold;
  color: black;
}

.nav a {
  text-decoration: none;
  color: #000;
  margin: 0 20px;
  font-size: 1.2em;
}
/* Confirmation Page */

.confirmation {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  align-items: center;
}
.proyectos {
  width: 100%;
  height: 100vh;
  display: flex;
